.background{
    background: linear-gradient(180deg, var(--clr-background-900), var(--clr-background-700) 20em);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;


    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    & .background-content{
        display: flex;
        justify-content: center;
        align-items: center;

        width: 100%;
        max-height: 50vh;
        max-width: var(--max-width);
        margin-right: 3em;

        & div{
            display: flex;
            justify-content: center;
            align-items: center;
            
            background-color: var(--clr-background-700);
            filter: brightness(105%);
            
            box-shadow: 2px 2px 12px -4px var(--clr-background-100);
            width: var(--size);
            height: var(--size);
            margin: 1rem -.8rem;
            border-radius: 50%;
            font-size: max(2vw, 1em);
            font-family: 'Courier New', Courier, monospace;
            font-weight: bold;
            color: var(--clr-background-100);

            aspect-ratio: 1;
            padding: 1rem;
        }

        & div:nth-of-type(2){
            margin-bottom: -2em;
        }
    }

    .logo{
        fill: var(--clr-background-100);
        width: calc(12vw + 13rem);

        margin: 6vw;
    }
}