body{
    display: flex;
    justify-content: center;
    min-height: 100vh;

    margin-top: calc(26vw + 10rem);
}

#root{
    width: 100%;
    max-width: 1500px;
    padding: 1rem;

    section>*{
        margin-bottom: 1rem;
    }
}

.filtering{
    display: flex;
    flex-direction: column;
    background-color: var(--clr-background-500);
    margin-bottom: 1rem;
    border-radius: 1rem;
    
    /* ----SEARCH---- */
    
    .search-input{
        display: flex;
        align-items: center;
    }
    
    .search-text{
        margin-left: 1em;
        flex-grow: 1;

        background-color: none;
        border: none;
        outline: none;

        color: var(--clr-text);

        font-size: larger;
        background: unset;
    }
    
    /* ----CATEGORY---- */
    
    .category-input{
        display: flex;
        align-items: flex-start;
    }
    
    .category-input .category-list{
        display: flex;
        flex-flow: row wrap;
        margin-left: 1em;
    }
}

.layout-grid{
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: 1fr 1fr;
    padding: 1em;
    background-color: var(--clr-background-700);
    border-radius: 1rem;

    @media (min-width: 500px) {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
}

.layout-list{
    display: flex;
    flex-direction: column;

    >*{
        margin-bottom: 1rem;
    }
}
